@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:ital,wght@0,400;0,700;0,900;1,400&family=Saira+Extra+Condensed:wght@700&display=swap);.col,.container {
  padding: 0 var(--ifm-spacing-horizontal);
  width: 100%
}

.markdown>h2,.markdown>h3,.markdown>h4,.markdown>h5,.markdown>h6 {
  margin-bottom: calc(var(--ifm-heading-vertical-rhythm-bottom)*var(--ifm-leading))
}

.markdown li,body {
  word-wrap: break-word
}

body,ol ol,ol ul,ul ol,ul ul {
  margin: 0
}

pre,table {
  overflow: auto
}

blockquote,pre {
  margin: 0 0 var(--ifm-spacing-vertical)
}

.breadcrumbs__link,.button {
  transition-timing-function: var(--ifm-transition-timing-default)
}

.button,code {
  vertical-align: middle
}

.button--outline.button--active,.button--outline:active,.button--outline:hover,:root {
  --ifm-button-color: var(--ifm-font-color-base-inverse)
}

.menu__link:hover,a {
  transition: color var(--ifm-transition-fast) var(--ifm-transition-timing-default)
}

.navbar--dark,:root {
  --ifm-navbar-link-hover-color: var(--ifm-color-primary)
}

.menu,.navbar-sidebar {
  overflow-x: hidden
}

:root,html[data-theme=dark] {
  --ifm-color-emphasis-500: var(--ifm-color-gray-500)
}

table thead,table tr:nth-child(2n) {
  background-color: var(--ifm-table-stripe-background)
}

.avatar__photo,.card,.tactics-app .tabs,.text--truncate {
  overflow: hidden
}

.button,.dropdown__link,.text--truncate,.tooltip-link {
  white-space: nowrap
}

.toggleButton_gllP,html {
  -webkit-tap-highlight-color: transparent
}

*,.loadingRing_RJI3 div {
  box-sizing: border-box
}

.clean-list,.containsTaskList_mC6p,.details_lb9f>summary,.dropdown__menu,.menu__list {
  list-style: none
}

